.icon-arrow-right {
  fill: #8bc34a;
}

.screen2-select {
  box-shadow: 0 0 0 2px #8bc34a;
}

.screen2-select__option:not(:last-child) {
  border-bottom: 2px solid #8bc34a;
}

.screen2-select__option:hover {
  background: #8bc34a;
}

.button-year,
.button-marka,
.button-model {
  box-shadow: 0 0 0 2px #8bc34a;
}

.button-filter,
.button-year,
.button-marka,
.button-model {
  background: #8bc34a;
}

.card-footer__question {
  border: 2px solid #8bc34a;
  border-radius: 15px;
  margin-right: 5px;
  padding: 0.75rem 1.09rem;
  cursor: pointer;
}

.card-footer__more {
  color: #8bc34a;
}

.icon-full-arrow-down {
  fill: #8bc34a;
}

.card-modal {
  position: relative;
  bottom: 55px;
  background: #8bc34a;
  align-items: center;
  display: none;
  flex-flow: column wrap;
  padding: 1.5rem 1.25rem 1.25rem;
  margin: 0 -1.25rem;
}

.card-modal--yellow {
  background: #fdd835 !important;
}

.card-modal--blue {
  background: #46bcec !important;
}

.card-modal--white {
  background: #fff !important;
}

.card-modal--rel {
  margin: 0;
  bottom: 0;
}

.card-modal--pos-right {
  margin: 0 -1.25rem;
}

.card-modal__inner {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-flow: column wrap;
  min-width: 280px;
}

.card-modal__title {
  font-weight: 900;
  font-size: 1rem;
  line-height: 1.4rem;
  padding-bottom: 17px;
  width: 100%;
  text-align: center;
}

.card-modal__date {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 335px;
  width: 100%;
  margin: 5px 0;
}

.card-modal__date-right,
.card-modal__date-left {
  flex: 1;
  border: 2px solid #fff;
  padding: 14px 10px;
  font-weight: 400;
  font-size: 0.9rem;
  line-height: 1rem;
  background: #8bc34a;
}

.card-modal__date-right {
  border-radius: 0 15px 15px 0;
  border-left: 0;
}

.card-modal__date-left {
  border-radius: 15px 0 0 15px;
}

.card-modal__item {
  font-weight: 400;
  font-size: 0.9rem;
  line-height: 1rem;
  border-radius: 15px;
  border: 2px solid #fff;
  background: #8bc34a;
  margin: 5px 0;
  padding: 0.9rem 0;
  width: 100%;
  max-width: 335px;
}

.card-modal__item--yellow {
  background: #fdd835 !important;
}

.card-modal__item--white {
  background: #fff !important;
  border: 2px solid #f2f2f2 !important;
}

.card-modal__item--dark {
  background: #000 !important;
  color: #fff !important;
}

.card-modal__item--blue {
  background: #46bcec !important;
}

.card-modal__button {
  font-weight: 900;
  font-size: 1.1rem;
  line-height: 1.3rem;
  background: #fff;
  color: #000;
  order: 1000;
}

.card-modal__button--mtop {
  margin: 2.8rem 0;
}

.card-modal__close {
  position: relative;
  width: 15px;
  height: 24px;
  margin-top: 15px;
}

.card-modal__close span {
  position: absolute;
  height: 4px;
  width: 24px;
  background: #000;
  transform: rotate(45deg);
  right: 0;
}

.card-modal__close::before {
  position: absolute;
  content: "";
  height: 4px;
  width: 24px;
  background: #000;
  transform: rotate(-45deg);
  right: 0;
}

.modal-upload {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: row wrap;
  order: 999;
  width: 100%;
}

.modal-upload__text {
  margin-left: 10px;
  font-weight: 900;
  font-size: 1rem;
  line-height: 1.2rem;
}

.footer-list__item a {
  color: #8bc34a;
}

.icon-envelope {
  fill: #8bc34a;
}

@media (min-width: 768px) {
  .card-modal {
    position: fixed;
    margin: 0;
    z-index: 1;
    border-radius: 15px;
    box-shadow: 0px 20px 50px rgba(0, 0, 0, 0.15);
  }

  .card-modal__inner {
    min-width: 335px;
  }

  .card-modal--rel {
    position: relative;
    border-radius: 0;
  }

  .card-modal__title {
    flex: 100%;
    text-align: center;
  }

  .modal-upload__text {
    margin-right: auto;
  }

  .icon-images {
    margin-left: auto;
  }
}

@media (min-width: 768px) and (orientation: landscape) {
  .card-modal--transform {
    transform: scale(0.8);
    bottom: -20px;
  }
}

@media (min-width: 1200px) {
  .card-modal--pos-right {
    margin: 0 -80px;
  }
}

@media (min-width: 1200px) and (orientation: landscape) {
  .card-modal--transform {
    transform: scale(1);
    bottom: 55px;
  }
}

@media (min-width: 1440px) {
  .header-list__item a:hover {
    color: #8bc34a;
  }

  .button-price {
    background: #8bc34a;
    box-shadow: 0 0 0 2px #8bc34a;
  }

  .button-price:focus {
    background: #fff;
    box-shadow: 0 0 0 2px #8bc34a;
    outline: none;
  }

  .screen2-select-price {
    top: 49%;
  }

  .card-modal {
    width: 383px;
  }

  .card-modal--pos-right {
    box-shadow: none;
  }

  .card-modal--rel {
    flex-flow: row wrap;
    justify-content: center;
    padding: 3.1rem 1.7rem 4rem;
    margin: 0;
  }

  .card-modal__inner {
    max-width: 1280px;
    width: 100%;
  }

  .card-modal__inner--flex-row {
    flex-flow: row wrap;
  }

  .card-modal__title {
    font-size: 1.5rem;
    line-height: 29px;
  }

  .card-modal__item {
    flex: 1;
  }

  .card-modal__item--mr40:not(:last-child) {
    margin-right: 40px;
  }

  .card-modal__button--mtop {
    margin: 2.8rem 0 0;
  }

  .modal-upload {
    order: 0;
  }
}